.rotateArrow {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 17px;
  top: 10px;
}
.adminMenuContainer .rotateArrow::before {
  content: "";
  width: 12px;
  height: 2px;
  position: absolute;
  right: 0;
  top: 6px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background-color: #bdbdbd;
}
.adminMenuContainer .rotateArrow::after {
  content: "";
  width: 12px;
  height: 2px;
  position: absolute;
  top: 14px;
  right: 0px;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  background-color: #bdbdbd;
}
